import React, { useState } from 'react';
import Button from '@components/libs/Button';
import Modal from '@modules/Modal';
import { Input } from '@components/libs/Forms';
import Structure from '../Structure';

import SenderDetails from './SenderDetails';
import Authe from './Authe';
import RequestAction from './RequestAction';
import './index.css';

export default function HomePage() {
    const [state, setState] = useState({
        userTextToScan: '',
        name: '',
        email: '',
        phoneNumber: '',
        socialMediaLink: '',
        scamSource: '',
        scamSourceData: false,
        alreadyScammed: '',
        scammedAmount: '',
        onRequest: false,
        getUserDetailsModal: false,
        autheModal: false,
        requestActionModal: false,
    });

    const [page, setPage] = useState('documents');

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const Description = () => {
        return <div className="desc">Scan for FRAUDULENT Documents, Phone Number and Email</div>;
    };
    return (
        <>
            <Structure description={<Description />}>
                <div className="tab_container">
                    <div
                        className={`tab_button ${page == 'documents' && 'active'}`}
                        onClick={() => setPage('documents')}
                    >
                        Documents
                    </div>
                    <div
                        className={`tab_button ${page == 'phoneNumber' && 'active'}`}
                        onClick={() => setPage('phoneNumber')}
                    >
                        Phone Number
                    </div>
                    <div className={`tab_button ${page == 'email' && 'active'}`} onClick={() => setPage('email')}>
                        Email
                    </div>
                </div>

                {page == 'documents' && (
                    <div className="verification_form_box">
                        <div className="verification_box_content">
                            <textarea
                                value={state.userTextToScan}
                                onChange={(e) => stateHandler('userTextToScan', e.target.value)}
                                className="verification_form_input"
                                placeholder="Paste Document text here..."
                            ></textarea>
                            <div className="button_container">
                                <Button title="Continue" onClick={() => stateHandler('getUserDetailsModal', true)} />
                            </div>
                        </div>
                    </div>
                )}

                {page == 'phoneNumber' && (
                    <>
                        <Input placeholder="Phone Number" onChange={(e) => stateHandler('password', e.target.value)} />
                        <div className="button_container">
                            <Button title="Continue" onClick={() => stateHandler('getUserDetailsModal', true)} />
                        </div>
                    </>
                )}

                {page == 'email' && (
                    <>
                        <Input placeholder="Email" onChange={(e) => stateHandler('password', e.target.value)} />
                        <div className="button_container">
                            <Button title="Continue" onClick={() => stateHandler('getUserDetailsModal', true)} />
                        </div>
                    </>
                )}
            </Structure>

            <Modal
                visible={state.getUserDetailsModal}
                onRequestClose={() => stateHandler('getUserDetailsModal', false)}
            >
                <SenderDetails
                    onRequestClose={() => stateHandler('getUserDetailsModal', false)}
                    stateHandler={stateHandler}
                    state={state}
                />
            </Modal>
            <Modal visible={state.autheModal} onRequestClose={() => stateHandler('autheModal', false)}>
                <Authe onRequestClose={() => stateHandler('autheModal', false)} stateHandler={stateHandler} />
            </Modal>
            <Modal visible={state.requestActionModal} onRequestClose={() => stateHandler('requestActionModal', false)}>
                <RequestAction onRequestClose={() => stateHandler('requestActionModal', false)} state={state} />
            </Modal>
        </>
    );
}
