import React, { useState, useEffect } from 'react';
import { Input, Select } from '@components/libs/Forms';
import ModalPage from '@components/layouts/ModalPage';
import SojiBaseInit from '@modules/SojiBase';
import Cookies from 'js-cookie';
import './index.css';

const scammedOptionObject = [
    {
        label: 'Yes',
        value: 'Yes',
    },
    {
        label: 'No',
        value: 'No',
    },
];

export default function SenderDetails({ onRequestClose, stateHandler, state }) {
    const [getIsLoading, setIsLoading] = useState(false);
    const [error, setError] = useState();

    const actionButton = async () => {
        if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
            onRequestClose();
            stateHandler('requestActionModal', true);
        } else {
            onRequestClose();
            stateHandler('autheModal', true);
        }
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/utils/scam_source');
            getter.on('static', async (response) => {
                stateHandler('scamSourceData', response.response.data);
            });
        })();
    }, []);

    return (
        <ModalPage
            isPageLoaded={state.scamSourceData}
            pageTitle="Tell Us more Information about the document"
            buttonTitle="Scan through Document"
            isButtonLoading={getIsLoading}
            actionButton={actionButton}
            onRequestClose={onRequestClose}
            error={error}
            className="sender_details"
        >
            {state.scamSourceData && (
                <div className="form_box_container">
                    <div className="row form_box">
                        <Input
                            placeholder="Enter the Full Name Here"
                            onChange={(e) => stateHandler('name', e.target.value)}
                            title="Is any name attached to the document?"
                            value={state.name}
                        />
                        <div className="col-md-6">
                            <Input
                                placeholder="Enter the Email Here"
                                title="Is any email address attached to the document?"
                                onChange={(e) => stateHandler('email', e.target.value)}
                                value={state.email}
                            />
                        </div>
                        <div className="col-md-6">
                            <Input
                                placeholder="Enter the Phone Number Here"
                                title="Is any contact phone number provided as well?"
                                onChange={(e) => stateHandler('phoneNumber', e.target.value)}
                                value={state.phoneNumber}
                            />
                        </div>

                        <div className="col-md-6">
                            <Input
                                placeholder="Social Media Link"
                                title="If any Social Media Link were shared, Please provide it below"
                                onChange={(e) => stateHandler('socialMediaLink', e.target.value)}
                                value={state.socialMediaLink}
                            />
                        </div>

                        <div className="col-md-6">
                            <Select
                                placeholder="Select Scam Source"
                                title="Scam Source"
                                options={state.scamSourceData}
                                onChange={(e) => stateHandler('scamSource', e.value)}
                                value={state.scamSourceData.filter(function (option) {
                                    return option.value === state.scamSource;
                                })}
                            />
                        </div>

                        <div className="col-md-6">
                            <Select
                                title="Have you been scammed with this document?"
                                onChange={(e) => stateHandler('alreadyScammed', e.value)}
                                options={scammedOptionObject}
                                value={scammedOptionObject.filter(function (option) {
                                    return option.value === state.alreadyScammed;
                                })}
                            />
                        </div>

                        <div className="col-md-6">
                            <Input
                                disabled={state.alreadyScammed != 'Yes'}
                                title="How much were you scammed?"
                                onChange={(e) => stateHandler('scammedAmount', e.target.value)}
                                value={state.scammedAmount}
                                
                            />
                        </div>
                    </div>
                </div>
            )}
        </ModalPage>
    );
}
