import React from 'react';
import styled from 'styled-components';
import Preloader from '@components/preloader';
import { MdClose } from 'react-icons/md';
import { colors } from '@components/typo';

import Button from '@components/libs/Button';

const Container = styled.div`
    max-height: 85vh;
    min-width: 400px;
    background-color: #fff;
    border-radius: 15px;
    padding: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
`;
const TitleContainer = styled.div`
    width: 100%;
    text-align: left;
    font-size: 17px;
    padding: 10px;
    border-bottom: 1px solid #f9f9f8;
    color: ${colors.defaultColor};
`;
const FormContainer = styled.div`
    width: 100%;
    padding: 10px;
    overflow-y: auto;
    overflow-x: hidden;
    flex: 1;
`;

const InviteButtonContainer = styled.div`
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
`;

const InviteButton = styled(Button)`
    width: 400px;

`;

const CloseButton = styled.div`
    background: #ffeed5;
    height: 30px;
    width: 30px;
    border-radius: 15px;
    position: absolute;
    top: 18px;
    right: 15px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const ErrorText = styled.p`
    color: red;
`;

export default function ModalPage({
    onRequestClose,
    isPageLoaded,
    pageTitle,
    children,
    error,
    isButtonLoading,
    buttonTitle,
    actionButton,
    className
}) {
    return (
        <Container className={className}>
            {isPageLoaded ? (
                <>
                    <TitleContainer>{pageTitle}</TitleContainer>
                    <CloseButton onClick={() => onRequestClose()}>
                        <MdClose color={colors.defaultColor} size={16} />
                    </CloseButton>
                    <FormContainer>{children}</FormContainer>
                    {error && <ErrorText>Error Occured: {error}</ErrorText>}
                    <InviteButtonContainer>
                        <InviteButton title={buttonTitle} loading={isButtonLoading} onClick={() => actionButton()} />
                    </InviteButtonContainer>
                </>
            ) : (
                <Preloader />
            )}
        </Container>
    );
}
