import React, { useState } from 'react';
import styled from 'styled-components';

import Login from '@pages/LandingPage/Authe/Pages/Login';
import SignUp from '@pages/LandingPage/Authe/Pages/SignUp';
import ForgotPassword from '@pages/LandingPage/Authe/Pages/ForgotPassword';

const Structure = styled.div`
    width: 500px;
    max-height: 95vh;
    background-color: #fff;
    overflow-y: auto;
    border-radius: 15px;
    display: flex;
`;

const Authe = ({ onRequestClose, stateHandler }) => {
    const [state, setState] = useState({
        pageName: 'login',
    });

    const pageHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const embedToGetUserToken = async () => {
        onRequestClose();
        stateHandler('requestActionModal', true);
    };

    return (
        <Structure>
            {state.pageName === 'login' && (
                <Login pageHandler={pageHandler} embedToGetUserToken={embedToGetUserToken} />
            )}
            {state.pageName === 'signup' && (
                <SignUp pageHandler={pageHandler} embedToGetUserToken={embedToGetUserToken} />
            )}
            {state.pageName === 'forgotPassword' && <ForgotPassword pageHandler={pageHandler} />}
        </Structure>
    );
};

export default Authe;
